@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 15px 30px;
  cursor: default;
}

header img {
  height: 48px;
  width: 48px;
}

header .primaryText {
  font-size: 1.1rem;
  font-weight: bold;
}

header .secondaryText {
  font-size: 0.75rem;
  font-weight: bold;
  color: #bbbbbd;
}

header #usernameDateTime {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

header .content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 1rem;
}

header .svgContainer {
  background-color: white;
  border: 1px solid black;
  border-radius: 50%;
  padding: calc(36px - 20px);
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  transition: background-color 0.4s, fill 0.4s;
}

header .svgContainer:hover {
  background-color: black;
  border: 1px solid white;
  fill: white;
}

header nav ul {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
}

header nav ul li a {
  text-transform: uppercase;
  font-weight: 500;
  color: #9e9e9e;
  transition: 0.25s color;
  text-decoration: none;
  margin-left: 1.1rem;
  font-size: 1.1rem;
}

header nav ul li a:hover {
  color: #000;
}

main section#content {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  max-height: 100%;
}

main section.detail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

main #logo {
  height: 2rem;
  width: auto;
}

main section#detail #detail__title {
  font-size: 2.5rem;
}

main section#detail #detail__info {
  margin: 1rem 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

main section#detail #detail__info span {
  margin-right: 0.75rem;
  font-weight: bold;
}

main section#detail #detail__about {
  color: #9e9e9e;
}

main section#actions {
  display: grid;
  grid-template-columns: repeat(2, 40%);
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 5rem;
}

main section#actions #actions__action-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.75rem 1.25rem 0.75rem 0;
  margin: 1rem 0;
  background-color: #f9f9f9;
  border-radius: 8px;
  cursor: pointer;
  transition: box-shadow 0.5s, background-color 0.5s, color 0.5s;
  font-weight: bold;
  color: #b9bbba;
}

main section#actions #actions__action-container:hover {
  box-shadow: 0px 0px 20px 0px rgb(0 0 0 / 19%);
  background-color: #ffffff;
  color: #000;
}

main section#actions #actions__action-container svg {
  fill: #b9bbba;
  margin: 0 0.75rem;
  transition: fill 0.5s;
}

main section#actions #actions__action-container:hover svg {
  fill: #f00;
}

img#aside-image {
  position: fixed;
  right: 13%;
  top: 15%;
  width: auto;
  height: 100%;
}

#aside-background {
  background-color: #03000f;
  position: absolute;
  top: 0;
  right: 0;
  height: 100vh;
  width: 25vw;
  z-index: -1;
}

.liked {
  color: #f00 !important;
}

.liked svg {
  fill: #f00 !important;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

body {
  font: 400 14px "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  max-height: 100vh;
}

img {
  max-width: 100%;
  height: auto;
}

header {
  height: 10vh;
}

main.container {
  padding: 0 13%;
  height: 90vh;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;

  cursor: default;
}

